// @flow

import React, {useState, useEffect} from 'react'
import {ThemeProvider} from 'styled-components'
import {CssBaseline, Typography} from '@material-ui/core'
import {createMuiTheme} from '@material-ui/core/styles'
import {I18n} from 'aws-amplify'

import {GlobalStyle, Container} from './styles'

import SymbolRow from './symbol-row'

const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#757ce8',
      main: '#3f50b5',
      dark: '#002884',
      contrastText: '#fff',
    },
    secondary: {
      light: '#ff7961',
      main: '#f44336',
      dark: '#ba000d',
      contrastText: '#000',
    },
  },
})

const RADIAN_RATE: number = 0.028
// const INTERVAL_MS: number = 170

const Se = () => {
  const [ballWidth, setBallWidth] = useState<number>(0)
  const [ballHeight, setBallHeight] = useState<number>(0)

  const handleOnResize = () => {
    setBallWidth(window.innerWidth * RADIAN_RATE)
    setBallHeight(window.innerHeight * RADIAN_RATE)
  }

  useEffect(() => {
    setBallWidth(window.innerWidth * RADIAN_RATE)
    setBallHeight(window.innerHeight * RADIAN_RATE)
    window.addEventListener('resize', handleOnResize)

    return () => {
      window.removeEventListener('resize', handleOnResize)
    }
  }, [])

  return (
    <>
      <CssBaseline />
      <GlobalStyle />
      <ThemeProvider theme={theme}>
        <Container>
          <SymbolRow activated height={ballHeight} width={ballWidth} />

          <Typography variant="h4">{I18n.get('msgEyeTrainingFinish')}</Typography>
        </Container>
      </ThemeProvider>
    </>
  )
}

export default Se
